/*============================================================== 
 For all pages 
 ============================================================== */
#main-wrapper {
  width: 100%;
  overflow: hidden;
  position: relative;
}

.page-wrapper {
  background: #f9fbfd;
  position: relative;
  transition: 0.2s ease-in;
  display: none;
  box-shadow: 0px 3px 9px 0px rgba(162, 176, 190, 0.15);
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

.page-wrapper > .container-fluid {
  padding: 35px;
  min-height: calc(100vh - 210px);
}

/*******************
 Footer
*******************/
.footer {
  padding: 15px 20px;
}

/*******************
 Stickey inner-left-part
*******************/
.left-part {
  height: 100%;
  width: 260px;
  position: absolute;
  border-right: 1px solid #edf2f9;
}
.left-part.fixed-left-part {
  position: fixed;
  top: 0px;
  padding-top: 10px;
}
.left-part .show-left-part {
  position: absolute;
  top: 45%;
  right: -41px;
}

.right-part {
  width: calc(100% - 260px);
  min-height: calc(100vh - 64px);
  overflow: auto;
  margin-left: 260px;
}

.reverse-mode .left-part {
  right: 0;
  border-left: 1px solid #edf2f9;
}

.reverse-mode .show-left-part {
  right: auto;
  left: -41px;
}

.reverse-mode .right-part {
  margin-left: 0px;
  margin-right: 260px;
}

